import request from './request.js'

const baseUrl = CONFIG_SERVICE.baseUrl

export function getSectionList() {
  const url = baseUrl + '/section/list'
  return request.get(url, {})
}

export function getSectionListExt(sectionId) {
  const url = baseUrl + '/section/listext'
  return request.get(url, {
    sectionId: sectionId
  })
}
